import MyTabbar from '../../components/my-components/myTabbar3'
import selectItem from '../../components/my-components/editBlock-SelectItem.vue'
import inputItem from '../../components/my-components/editBlock-InputItem.vue'
import {
	getJurisdiction
} from '../../components/my-components/getJurisdiction.js'
import {
	bindWx
} from '../index/components/bindWx.js'
let DataFrom = {};
export default {
	components: {
		selectItem,
		inputItem,
		MyTabbar
	},
	data() {
		return {
			Jurisdiction: {}, //权限
			userName: '',
			userTel: '',
			userId: '',
			contact_mobile: '',
			userRoleName: '',
			realName: '',
			wxInfo: {},
			wxapp_openid:''
		}
	},
	methods: {
		initPage() {
			this.Request('myInfo').then(res => {
				this.userName = res.user.contact;
				this.userTel = res.user.username;
				this.contact_mobile = res.user.contact_mobile;
				this.userRoleName = res.user.is_root == 1 ? '超级管理员' : res.user.role_name;
				this.realName = res.user.contact;
				this.userId =res.user[res.user.account_type]||res.user.mobile||res.user.email||res.user.username;
				this.wxapp_openid=res.user.wxapp_openid;
			}) 
		},
		toPage(val) {
			if (val == 'name') {
				this.Cacher.setData('myself', {
					userName: this.realName,
					userTel: this.contact_mobile,
					userRoleName: this.userRoleName,
					realName: this.realName,
					needChange: {
						name: '姓名',
						id: 'realName'
					}
				})
				uni.navigateTo({
					url: '../../pagesSelf/pages/myName?from=myself'
				})
			} else if (val == 'tel') {
				this.Cacher.setData('myself', {
					userName: this.realName,
					userTel: this.contact_mobile,
					userRoleName: this.userRoleName,
					realName: this.realName,
					needChange: {
						name: '联系方式',
						id: 'userTel'
					}
				})
				uni.navigateTo({
					url: '../../pagesSelf/pages/myName?from=myself'
				})
			} else if (val == 'password') {
				uni.navigateTo({
					url: '../../pagesSelf/pages/password?from=myself'
				})
			}
		}, 
		reBindWX() { 
			this.closePageLoading();
			bindWx.call(this, true).then(res=>{
				
				this.initPage();
			});
		},
		leave() {
			this.closePageLoading();
			this.Dialog.confirm({
				title: '',
				message: '您确认退出当前账号吗？'
			}).then(() => {
				this.Cacher.setData('self', {
					from: 'self'
				})
				uni.reLaunch({
					url: '../login/index?from=self'
				})
			}).catch(() => {
				// on cancel
			});
		}
	},
	onShow() {
		let info = this.Cacher.getData('login') || {};
		this.wxInfo = info.userInfo;
		this.initPage();
	},
	onPullDownRefresh(){ 
		this.initPage();
	},
	onLoad() {
		uni.hideTabBar({ //隐藏tabbar
			animation: false
		});
		getJurisdiction.call(this).then(res => {
			this.Jurisdiction = res;
		}).catch(res => {
			this.Toast(res.message)
		})
	}
}
